.section {
  /* max-width: 600px; */
  overflow: hidden;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
}

.list {
  margin: 0;
  padding: 0 8px 0 0;
  box-sizing: border-box;
  overflow-y: auto;
  position: relative;
  display: flex;
  flex-direction: column;
}

.list::-webkit-scrollbar {
  background-color: #2f2f37;
  width: 8px;
}

.list::-webkit-scrollbar-thumb {
  background-color: #8585ad;
}

.list li {
  margin-bottom: 16px;
}